* {
  margin: 0;
  padding: 0;
}
.App {
  width: 100%;
  height: 100vh;
  display: grid;
  place-items: center;
}

.container {
  max-width: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1em;
}

img {
  width: 100%;
}

button {
  width: 100%;
  padding: 0.5em;
  font-size: 1em;
  background-color: white;
  border-radius: 5px;
  transition: 500ms;
}

button:hover {
  cursor: pointer;
  background-color: #ddd;
}
